import { Component, Element, h, Host, Prop } from '@stencil/core';
import { Tooltip } from 'bootstrap/dist/js/bootstrap.bundle';

@Component({
  tag: 'storm-tooltip',
  styleUrl: 'storm-tooltip.css',
  shadow: false,
})
export class StormTooltip {
  private _tooltip: Tooltip;

  @Element() host: HTMLElement;
  @Prop() heading: string;
  @Prop() placement: 'auto' | 'top' | 'right' | 'bottom' | 'left' = 'bottom';
  @Prop() trigger: 'click' | 'hover' | 'focus' | 'manual' = 'hover';

  componentDidRender() {
    this._tooltip?.dispose();
    if (this.heading) {
      this._tooltip = new Tooltip(this.host.querySelector('div'), {
        title: this.heading,
        placement: this.placement,
        trigger: this.trigger,
      });
      this.host.querySelector('div').addEventListener('click', () => {
        this._tooltip.hide();
      });
    }
  }

  render() {
    return (
      <Host>
        <div class="d-inline-block">
          <slot />
        </div>
      </Host>
    );
  }

  disconnectedCallback() {
    this._tooltip?.dispose();
  }
}
